import React, { Component } from 'react';
import {Helmet} from "react-helmet";
import Cover from './Cover'
import Timeline from './Timeline'
import Events from './Events'

class Home extends Component {
  render() {
    return (
      <div>
        <Helmet>
            <title>Our Island</title>
        </Helmet>
        <Cover/>
        <Timeline/>
        <Events/>
      </div>
    );
  }
}

export default Home;



