import React, { Component } from 'react';
import {Helmet} from "react-helmet";

class Page1 extends Component {
  render() {
    return (
      <div>
        Page1
        <Helmet>
            <title>Page1</title>
        </Helmet>
      </div>
    );
  }
}

export default Page1;



